.fancybox-custom-modal .fancybox__slide {
  padding: 15px;
}
@media (max-width: 425.98px) {
  .fancybox-custom-modal .fancybox__slide {
    padding: 0;
  }
}
.fancybox-custom-modal .fancybox__content,
.fancybox-custom-modal .fancybox__container {
  color: var(--color-text);
}
.fancybox-custom-modal .fancybox__content {
  padding: 0;
  background: none;
}
@media (max-width: 425.98px) {
  .fancybox-custom-modal .fancybox__content {
    width: 100%;
    border-radius: 16px 16px 0 0;
  }
}
.fancybox-custom-modal .carousel__button.is-close {
  position: fixed;
  right: 0;
  top: 0;
  padding: 16px;
  width: 68px;
  height: 68px;
  animation-delay: 0.35s !important;
}
@media (max-width: 425.98px) {
  .fancybox-custom-modal .carousel__button.is-close {
    position: absolute;
    width: 32px;
    height: 32px;
    top: 16px;
    right: 16px;
    padding: 0;
    border-radius: 50%;
    background: #F3F3F5;
  }
}
@media (max-width: 425.98px) {
  .fancybox-custom-modal .carousel__button.is-close svg {
    filter: none;
    color: #000;
    width: 12px;
    height: 12px;
  }
}

.grid.swiper .swiper-wrapper {
  flex-wrap: nowrap;
  /* width       : 100%; */
  /* min-width   : 0; */
  /* margin-left : 0; */
  /* margin-right: 0; */
}

.swiper {
  user-select: none;
}

img.swiper-lazy {
  transition: 0.2s ease;
}

img.swiper-lazy:not(.swiper-lazy-loaded) {
  opacity: 0;
}

.swiper-button-disabled {
  opacity: 0.4;
  pointer-events: none;
}

.swiper-slide {
  height: auto;
  align-self: stretch;
}

.slider {
  position: relative;
}

.slider-prev,
.slider-next {
  position: absolute !important;
  top: calc(50% - 30px);
  z-index: 3;
}
@media (max-width: 1117.98px) {
  .slider-prev,
.slider-next {
    top: calc(50% - 15px);
  }
}

.slider-next {
  left: calc(100% + 24px);
}
@media (max-width: 1231.98px) {
  .slider-next {
    left: calc(100% - 24px);
  }
}
@media (max-width: 679.98px) {
  .slider-next {
    left: auto;
    right: 2px;
  }
}

.slider-prev {
  right: calc(100% + 24px);
}
@media (max-width: 1231.98px) {
  .slider-prev {
    right: calc(100% - 24px);
  }
}
@media (max-width: 679.98px) {
  .slider-prev {
    right: auto;
    left: 2px;
  }
}

@media (max-width: 679.98px) {
  .slider--overwrap {
    width: calc(100% + 30px);
    margin-left: -15px;
    margin-right: -15px;
    padding-right: 15px;
    padding-left: 15px;
    overflow: hidden;
  }
}
@media (max-width: 679.98px) {
  .slider--overwrap .swiper {
    overflow: visible;
  }
}

