  color: var(--color-text);
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border-radius: 16px;
  padding: 8px;
}
.tippy-box[data-theme~=flat] .tippy-content {
  padding: 0;
}
.tippy-box[data-theme~=flat] .tippy-arrow {
  color: #fff;
}

.tippy-box[data-theme~=custom-default] {
  background-color: var(--bg3);
  color: var(--color-primary);
  font-weight: 600;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border-radius: 6px;
  padding: 12px 20px;
}
.tippy-box[data-theme~=custom-default] .tippy-content {
  padding: 0;
}
.tippy-box[data-theme~=custom-default] .tippy-arrow {
