  padding: 16px;
}
.tippy-box[data-theme~=header-user] .tippy-content {
  padding: 0;
}
.tippy-box[data-theme~=header-user] .tippy-arrow {
  color: var(--bg3);
}

.tippy-box[data-theme~=chart-col] {
  background-color: var(--bg3);
  color: var(--color-text);
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border-radius: 6px;
  padding: 12px 16px;
